.modal,
.modal_active {
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1000;
  /* Sit on top */
  padding-top: 10%;
  padding-left: 10px;
  padding-right: 10px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(81, 87, 105, 0.4);
  /* Black w/ opacity */
  visibility: hidden;
  opacity: 0;
  transition: 0.3s; }

.modal_active {
